.wrapper {
  padding-top: 50px;
  background: #90FF7F;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  width: 45%;
  max-width: 760px;
  margin: 0 auto;
  border-radius: 20px;
}

.navbar {
    margin-bottom: 5%;
    color: white;
    background-color: black;
}
.img-thumbnail {
    background: #fff;
    border-radius: 2px;
    height: 125px;
    margin: 1rem;
    position: relative;
    width: 125px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .img-thumbnail:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
body {
    background-image: url(/static/media/background.9ad5e071.png);
}
